<template>
  <div class="wrap">
    <!-- 导航条 -->
    <Header :left_msg="left" :middle_msg="middle" :right_msg="right"></Header>
    <!-- 公示 -->
    <div class="foods_wrap">
      <h1 class="foods_safety">
        <span class="note">食品监督安全公示</span>
      </h1>
      <div class="inspection_results">
        <div class="img_wrap">
          <img
            class="expression"
            src="data:image/png;base64,"
            alt
          >
        </div>
        <div class="inspection">
          <p class="check_result">
            <span>监督检查结果：</span>
            <span class="good">良好</span>
          </p>
          <p class="check_date">检查日期</p>
        </div>
      </div>
    </div>
    <ul class="company_store">
      <h1 class="company_info">工商登记信息</h1>
      <li class="company_address">企业名称</li>
      <li class="company_address">工商执照注册号</li>
      <li class="company_address">注册资本</li>
      <li class="company_address">注册地址</li>
      <li class="company_address">属地监管所</li>
      <li class="company_address">法定代表人</li>
    </ul>
  </div>
</template>

<script>
import Header from "../common/Header";
export default {
  data() {
    return {
      // 导航条数据
      left: "el-icon-arrow-left",
      middle: "食品监督安全公示",
      right: false,
      company_detail: this.$route.params.company_detail
    };
  },
  components: {
    Header
  }
};
</script>

<style scoped>
.wrap {
  background-color: #f1f1f1;
  overflow: hidden;
}
/* 认证 */
.foods_wrap {
  width: 100%;
  margin-top: 62px;
  background-color: white;
}
.foods_safety {
  width: 97%;
  height: 49px;
  border-bottom: 1px solid #f1f1f1;
  margin-bottom: 10px;
  color: #333;
  padding-left: 3%;
  line-height: 49px;
  font-size: 20px;
}
.note {
  width: 48%;
  text-align: left;
  display: inline-block;
}

.inspection_results {
  height: 76px;
  padding: 5px 3%;
  /* background-color: green; */
}
.img_wrap {
  height: 72px;
  width: 18%;
  /* background-color: antiquewhite; */
  float: left;
  margin-right: 3%;
}
.inspection {
  width: 79%;
  height: 72px;
  /* background-color: yellow; */
  float: left;
}
.inspection p {
  height: 36px;
  font-size: 15px;
  color: #666;
}
.check_result {
  line-height: 36px;
  /* background-color: pink; */
}
.check_date {
  line-height: 36px;
  /* background-color: pink; */
}
.company_store {
  background-color: white;
}
.company_info {
  height: 49px;
  margin-top: 10px;
  border-bottom: 1px solid #f1f1f1;
  color: #333;
  padding-left: 3%;
  line-height: 49px;
  font-size: 20px;
}
.company_address {
  width: 100%;
  height: 35px;
  line-height: 35px;
  margin-left: 3%;
  font-size: 14px;
  color: #666;
  border-bottom: none;
  margin-bottom: 0;
}

/* 表情及结果设置 */
.expression {
  height: 60px;
}
.good {
  color: #7ed321;
}
</style>



