
<template>
  <div class="wrap">
    <!-- 导航条 -->
    <i class="el-icon-arrow-left" style="z-index:1000;position:fixed;top:10px;left:10px;color:white;font-size:30px;" @click="got0()"></i>
    <Header :middle_msg="middle" :right_msg="right"></Header>
    <!--  -->
    <div class="activities">
      <ul class="act_wrap">
        <h1 class="act_title">活动与属性</h1>
        <li class="act_discount" v-for="(value,index) in nav_data. activities" :key="index">
          <span class="discount_mark" :style="'background-color:#'+value.icon_color">{{value.icon_name}}</span>
          <span class="discount_detail">{{value.description}}</span>
        </li>
        <li class="act_supports" v-for="value in nav_data. supports" :key="value.id">
          <span class="support_mark" :style="'background-color:#'+value.icon_color">{{value.icon_name}}</span>
          <span class="support_detail">{{value.description}}</span>
        </li>
      </ul>
    </div>
    <div class="foods_wrap">
      <h1 class="foods_safety">
        <span class="note">食品监督安全公示</span>
        <span class="identify">
          企业认证详情
          <i class="el-icon-arrow-right" @click="company_detail()"></i>
        </span>
      </h1>
      <div class="inspection_results">
        <div class="img_wrap">
<img class="expression" src="data:image/png;base64," alt="">
        </div>
        <div class="inspection">
          <p class="check_result">
            <span>监督检查结果：</span>
            <span class="good">良好</span>
          </p>
          <p class="check_date">检查日期</p>
        </div>
      </div>
    </div>
    <ul class="company_store" >
      <h1 class="company_info">商家信息</h1>
      <li class="company_address" >{{nav_data.name}}</li>
      <li class="company_address" >{{nav_data.address}}</li>
      <li class="company_address" >营业时间：{{nav_data.opening_hours[0]}}</li>
      <li class="foods_safety passport">
        <span class="note">营业执照</span>
        <span class="identify">
          <i class="el-icon-arrow-right passport"></i>
        </span>
      </li>
          <li class="foods_safety passport">
        <span class="note">餐饮服务许可证</span>
        <span class="identify">
          <i class="el-icon-arrow-right passport"></i>
        </span>
      </li>
    </ul>
  </div>
</template>

<script>
import Header from "../common/Header";
export default {
  data() {
    return {
      // 导航条数据
      left: "el-icon-arrow-left",
      middle: "商家详情",
      right: false,
      // element引入数据
      value4: null,
      //餐馆详情数据
      nav_data: this.$route.params.detail
    };
  },
  created() {
    console.log(this.nav_data, "ccc");
  },
  methods: {
    got0(){
      this.$router.push({name:"storeDetail"})
      console.log("!!!!")
    },
      company_detail(){
          this.$router.push({name:"companyDetail", params:{company_detail:this.nav_data.identification}})
      }
  },
  components: {
    Header
  }
};
</script>

<style>
.wrap {
  background-color: #f1f1f1;
  overflow: hidden;
}
/* 活动 */
.activities {
  /* position: relative;
  top: 52px; */
  margin-top: 62px;
  padding-bottom: 20px;
  background-color: white;
  overflow: hidden;
}

.act_title {
  height: 49px;
  margin-top: 10px;
  border-bottom: 1px solid #f1f1f1;
  margin-bottom: 10px;
  color: #333;
  padding-left: 3%;
  line-height: 49px;
  font-size: 20px;
}
.act_wrap li {
  width: 97%;
  height: 36px;
  margin-left: 3%;
  margin-bottom: 10px;
  font-size: 14px;
  color: #666;
}
.discount_mark{
    line-height: 36px;
    width: 5%;
    padding: 3px;
    border-radius: 3px;
    color: white;
    text-align: center;
    font-size: 12px;
    /* background-color: purple; */
    margin-right: 1%;
}
.support_mark{
    line-height: 36px;
    width: 5%;
    padding: 3px;
    border-radius: 3px;
    color: white;
    text-align: center;
    font-size: 12px;
    /* background-color: purple; */
    margin-right: 1%;
}

/* 认证 */
.foods_wrap {
  width: 100%;
  margin-top: 10px;
  background-color: white;
}
.foods_safety {
  width: 97%;
  height: 49px;
  border-bottom: 1px solid #f1f1f1;
  margin-bottom: 10px;
  color: #333;
  padding-left: 3%;
  line-height: 49px;
  font-size: 20px;
}
.note {
  width: 48%;
  text-align: left;
  display: inline-block;
}
.identify {
  width: 48.5%;
  text-align: right;
  color: #bbb;
  display: inline-block;
}
.inspection_results {
  height: 76px;
  padding: 20px 3%;
  /* background-color: green; */
}
.img_wrap {
  height: 72px;
  width: 18%;
  /* background-color: antiquewhite; */
  float: left;
  margin-right: 3%;
}
.inspection {
  width: 79%;
  height: 72px;
  /* background-color: yellow; */
  float: left;
}
.inspection p {
  height: 36px;
  font-size: 15px;
color: #666;
}
.check_result {
  line-height: 36px;
  /* background-color: pink; */
}
.check_date {
  line-height: 36px;
  /* background-color: pink; */
}
.company_store {
  background-color: white;
}
.company_info {
  height: 49px;
  margin-top: 10px;
  border-bottom: 1px solid #f1f1f1;
  color: #333;
  padding-left: 3%;
  line-height: 49px;
  font-size: 20px;
}
.company_address{
 width: 100%;
  height: 60px;
  line-height: 60px;
  margin-left: 3%;
  margin-bottom: 10px;
  font-size: 15px;
  color: #666;
  border-bottom: 1px solid #f1f1f1;
  /* background-color: red; */
}
.passport{
font-size: 15px;
color: #666;
}
/* 表情及结果设置 */
.expression{
    height: 60px;
}
.good{
    color: #7ed321;
}
</style>



